import React, { useState } from 'react';
import style from './style.module.css';
import Modal from '../Modal/Modal';
import { useMediaQuery } from 'react-responsive'
import Photo1 from './Browser1.jpg';
import Photo2 from './Browser2.jpg';
import Photo3 from './Browser3.jpg';
import Photo4 from './Browser4.jpg';
import Photo5 from './Browser5.jpg';
import Photo6 from './Browser6.jpg';
import Photo7 from './Browser7.jpg';
import Photo8 from './Browser8.jpg';
import Photo9 from './Browser9.jpg';
import Photo10 from './Browser10.jpg';
import Photo11 from './Browser11.jpg';
import Photo12 from './Browser12.jpg';


const PhotoBrowser = (props) =>{

    const [isShow, setShow] = useState(0);



    const isDesktopOrLaptop = useMediaQuery({ minDeviceWidth: 1224 });


    if (isShow === 1){
        return(
            <Modal show={isShow} close = {() => setShow(0)} photo = {1}/>
        );
    }


    if (isDesktopOrLaptop === true){
        return (
            <div className = {style.desktopWrapper}>

                 <div className = {style.searchBar}>
                    <div className = {style.searchUI}>
                        <h2 className = {style.searchLabel}>Поиск</h2>
                        <input className = {style.searchInput} type="text" placeholder = "🔍TripleF"></input>
                    </div>
                 </div>
                 <div className = {style.first}>
                    <div className = {style.galleryItem} onClick={() =>setShow(1)} >
                        <img src = {Photo1} className = {style.galleryItem} alt = "Gallery Item"/>
                    </div>
                    <div className = {style.galleryItem}>
                        <img src = {Photo2} className = {style.galleryItem} alt = "Gallery Item"/>
                    </div>
                    <div className = {style.galleryItem}>
                        <img src = {Photo3} className = {style.galleryItem} alt = "Gallery Item"/>
                    </div>    
                    <div className = {style.galleryItem}>
                        <img src = {Photo4} className = {style.galleryItem} alt = "Gallery Item"/>
                    </div>
                    <div className = {style.galleryItem}>
                        <img src = {Photo5} className = {style.galleryItem} alt = "Gallery Item"/>
                    </div>
                    <div className = {style.galleryItem}>
                        <img src = {Photo6} className = {style.galleryItem} alt = "Gallery Item"/>
                    </div> 
                    <div className = {style.galleryItem}>
                        <img src = {Photo7} className = {style.galleryItem} alt = "Gallery Item"/>
                    </div>
                    <div className = {style.galleryItem}>
                        <img src = {Photo8} className = {style.galleryItem} alt = "Gallery Item"/>
                    </div>
                    <div className = {style.galleryItem}>
                        <img src = {Photo9} className = {style.galleryItem} alt = "Gallery Item"/>
                    </div> 
                    <div className = {style.galleryItem}>
                        <img src = {Photo10} className = {style.galleryItem} alt = "Gallery Item"/>
                    </div> 
                    <div className = {style.galleryItem}>
                        <img src = {Photo11} className = {style.galleryItem} alt = "Gallery Item"/>
                    </div> 
                    <div className = {style.galleryItem}>
                        <img src = {Photo12} className = {style.galleryItem} alt = "Gallery Item"/>
                    </div> 
                </div>
                <div className = {style.second}>

                </div>
                <div className = {style.tabs}>
                    <div className = {style.tabItem}></div>
                    <div className = {style.tabItem}>📂</div>
                    <div className = {style.tabItem}>📆</div>
                    <div className = {style.tabItem}>✉️</div>
                    <div className = {style.tabItem}><div className = {style.selected}>🔍</div></div>
                    <div className = {style.tabItem}></div>
                </div>
            </div>
        );
    }else{
        return(
            <div className = {style.mobileWrapper}>
                    <div className = {style.searchBar}>

                    </div>
                    <div className = {style.first}>

                    </div>
                    <div className = {style.second}>

                    </div>
                    <div className = {style.tabs}>

                    </div>
            </div>
        );
    }
}

export default PhotoBrowser;