import style from './style.module.css';
import StyledText from '../StyledText/StyledText';
import { useMediaQuery } from 'react-responsive'



const Modal = (props) =>{

    const isDesktopOrLaptop = useMediaQuery({ minDeviceWidth: 1224 });

    if (isDesktopOrLaptop === true){

            if (props.photo === 1 ){
                const Img = 'Photo1';
            }


            if(props.show){
                return(
                    <div className = {style.desktopWrapper}>
                            <div className = {style.cardFrame}>

                                <div className = {style.photoWrapper}>
                                    <img className = {style.image} src = {'https://fantabulosity.com/wp-content/uploads/2018/06/How-to-Make-Pancakes-homemade-from-scratch-4-500x500.jpg'} alt = 'GalleryImgItem'/>
                                </div>
                                <div className = {style.photoDescription}>
                                    <div className = {style.exitButton}>
                                        <h2 className = {style.crossIcon} onClick={props.close}>✖️</h2>
                                    </div>
                                        <div className = {style.header}>
                                            <StyledText text = {'ЗАГОЛОВОК'}/>
                                        </div>
                                        <div className = {style.description}>
                                        <h2 className = {style.descriptionText}>
                                            Давно выяснено, что при оценке дизайна и композиции читаемый текст мешает сосредоточиться. Lorem Ipsum используют потому, что тот обеспечивает более или менее стандартное заполнение шаблона, а также реальное распределение букв и пробелов в абзацах, которое не получается при простой дубликации.
                                        </h2>
                                        </div>
                                    </div>
    
                            </div>
                    </div>
                );
            }else{
                return null;
            }

    }else{
        return(
            <div className = {style.mobileWrapper}>

            </div>
        );
    }
}

export default Modal;